<template>
  <div class="infoMent">
    <!-- 查询重置 -->
    <el-form :inline="true" class="demo-form-inline" size="small">
      <el-form-item>
        <el-button type="primary" @click="addStudent">新增</el-button>
      </el-form-item>
    </el-form>
    <!-- 查询重置 -->
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="name" label="姓名" align="center">
      </el-table-column>
      <el-table-column prop="sex" label="性别" align="center">
      </el-table-column>
      <el-table-column prop="age" label="年龄" align="center">
      </el-table-column>
      <el-table-column prop="father" label="父亲" align="center">
      </el-table-column>
      <el-table-column prop="mother" label="母亲" align="center">
      </el-table-column>
      <el-table-column prop="address" label="家庭住址" align="center">
      </el-table-column>
      <el-table-column prop="time" label="入校时间" align="center">
      </el-table-column>
      <el-table-column prop="phone" label="联系方式" align="center">
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button type="danger" size="mini" icon="el-icon-edit" @click="edit(scope.row)"></el-button>
          <el-button type="danger" size="mini" icon="el-icon-delete" @click="del(scope.row)"></el-button>

        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="收货地址" :visible.sync="dialogTableVisible">
      <el-table :data="gridData">
        <el-table-column property="date" label="日期" width="150"></el-table-column>
        <el-table-column property="name" label="姓名" width="200"></el-table-column>
        <el-table-column property="address" label="地址"></el-table-column>
      </el-table>
    </el-dialog>

    <!-- Form -->

    <el-dialog :title="state ? '添加学生信息' : '编辑学生信息'" :visible.sync="dialogFormVisible" width="500px">
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="姓名" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth" prop="sex">
          <el-radio v-model="form.sex" label="1">男</el-radio>
          <el-radio v-model="form.sex" label="2">女</el-radio>
        </el-form-item>
        <el-form-item label="年龄" :label-width="formLabelWidth" prop="age">
          <el-input v-model="form.age" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="父亲姓名" :label-width="formLabelWidth" prop="father">
          <el-input v-model="form.father" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="母亲姓名" :label-width="formLabelWidth" prop="mather">
          <el-input v-model="form.mother" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="家庭住址" :label-width="formLabelWidth" prop="address">
          <el-input v-model="form.address" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="入校时间" :label-width="formLabelWidth" prop="time">
          <el-date-picker v-model="form.time" format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd" type="date"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="联系方式" :label-width="formLabelWidth" prop="phone">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeInfo('form')">取 消</el-button>
        <el-button type="primary" @click="sure('form')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getData, changeInfo ,delData} from "@/utils/table";
export default {
  data() {
    return {
      tableData: [],
      form: {
        name: "",
        sex: "1",//默认值1
        age: "",
        father: "",
        mather: "",
        address: "",
        time: "",
        phone: "",
      },
      dialogFormVisible: false,
      rules: {
        name: [{ required: true, message: '请输入姓名' }],
        sex: [{ required: true }],
        age: [{ required: true, message: '请输入年龄' }],
        address: [{ required: true, message: '请输入地址' }],
        time: [{ required: true, message: '请输入入学时间' }],
        phone: [{ required: true, message: '请输入联系方式' }],
      },
      total: 0,//分页用，是显示的总条目数
      formLabelWidth: '80px',
      state: true,
    }
  },
  created() {
    getData(this, '/info')
  },
  methods: {

    edit(row) {
      this.form = { ...row }
      this.dialogFormVisible = true,
        this.state = false
      console.log(row)

    },
    del(row) {
      delData(this,'/info',row.id,getData)
    },
    closeInfo(form) {
      // 单独为取消设置清空验证和关闭弹窗
      this.dialogFormVisible = false
      this.$refs[form].resetFields()//清空验证

    },
    addStudent() {
      this.dialogFormVisible = true
      this.state = true
      this.form = {
        name: "",
        sex: "1",//默认值1
        age: "",
        father: "",
        mather: "",
        address: "",
        time: "",
        phone: "",

      }
      this.$refs['form'].resetFields()//清空验证

    },
    

    sure(form) {

      this.$refs[form].validate(valid => {
        if (valid) {
          // console.log(form, this, form)
          // 方法一
          // let methods = ''
          // this.state ? methods = 'post' : methods = 'put'
          // changeInfo(this, methods, '/info', this.form, getData)

          // -------------
          // 方法二
          if (this.state) {
            //通过state状态判断是否为新增还是修改，修改为else
            changeInfo(this, 'post', '/info', this.form, getData)
          } else {
            changeInfo(this, 'put', '/info', this.form, getData)

          }

        }
      })
    }
  },
}
</script>

<style lang="less" scoped>
.infoMent {
  .demo-form-inline {
    text-align: left;
  }

}
</style>